body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.page-wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: auto;
    white-space: nowrap;
    text-align: center;
    background: #fafafa;
}

.page-wrapper:before {
    height: 100%;
    display: inline-block;
    vertical-align: middle;
    content: '';
}
.content-wrapper {
    display: inline-block;
    white-space: normal;
    vertical-align: middle;
    text-align: left;
    padding: 10px 0;
}
.form-wrapper form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.form-wrapper .recaptcha-wrapper {
    margin-bottom: 15px;
}

.form-wrapper .form-head {
    width: 304px;
    color: #cfd2d6;
    margin-bottom: 20px;
}

.form-wrapper .form-head p {
    margin: 0;
}

.form-wrapper .form-control {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 25px;
}
.custom-input {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 15px;
}

.custom-input-logo {
    padding-right: 10px;
    padding-left: 10px;
    height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border: solid 1px #efefef;
    border-right: none;
}

.custom-input label{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background: #fff;
}

.custom-input input {
    padding: 0 0 0 5px;
    height: 40px;
    width: 255px;
    border: solid 1px #efefef;
    border-left: none;
    color: #999999;
    font-size: 14px;
}

.custom-input.input-error .custom-input-logo {
    border-left: solid 3px #fe0c5c;
    padding-left: 7px;
}
.custom-button {
    display: block;
    width: 200px;
    height: 60px;
    border-radius: 100px;
    border: none;
    font-size: 20px;
    text-transform: uppercase;
    color: #01172e;
    background: #26ffc7;
    cursor: pointer;
}

.custom-button:disabled {
    background: #f2f3f4;
    color: #c0c5cb;
    cursor: auto;
}
.error-container {
    text-align: center;
    color: #fe0c5c;
    margin-top: 20px;
}
.login-button {
    border: solid 2px #26ffc7;
    background: #01172e;
    color: #26ffc7;
    margin-bottom: 20px;
}

.login-button:hover {
    background: #26ffc7;
    color: #01172e;
}

.login-page {
    background: #01172e;
}
h1 {
    text-align: center;
}
